import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';

class Settings extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      disableInputs: true,
      profiles: [],
      isLoadingProfiles: true,
      viewId: props.viewId,
    };

    this.updateViewId = this.updateViewId.bind(this);
  }

  componentDidMount() {
    const { isKeyPairEnabled, isGaAccessEnabled, service } = this.props;

    if (!this.isValidatingOrFetching()) {
      this.setState({ disableInputs: false });
    }

    if (!isKeyPairEnabled || !isGaAccessEnabled) {
      return;
    }

    return service.fetchProfiles()
      .then(profiles => {
        this.setState({ profiles, isLoadingProfiles: false });
      });
  }

  isValidatingOrFetching() {
    const { error } = this.props;
    return (
      error && ( 'validation_validating_site' === error.code || 'site_fetching_historical_data' === error.code )
    );
  }

  updateViewId(e) {
    const { viewId } = this.state;
    // No view selected.
    if( '' === viewId ) {
      this.setState({ viewId: e.target.value });
    } else {
      const changeConfirmation = confirm('Are you sure you want to change the view ID? Doing so will remove all of your historical data and begin to populate Content Performance using the new view ID, which may take some time.');
      if ( true === changeConfirmation ) {
        this.setState({ viewId: e.target.value });
      }
    }
  }

  render() {
    const {
      optionGroup,
      optionNonce,
      referrer,
      isEnabled,
      dashboardUrl,
      isKeyPairEnabled,
      isGaAccessEnabled,
      isGaViewEnabled,
      configOption,
      accessKey,
      secretKey,
      defaultDateRange,
      defaultPubliedDateRange,
      capability,
      roles,
      keysUrl,
    } = this.props;

    const { disableInputs, profiles, isLoadingProfiles, viewId } = this.state;
    return (
      <div>
        <form method="post" action="options.php" encType="multipart/form-data">
          <input type='hidden' name='option_page' value={optionGroup} />
          <input type="hidden" name="action" value="update" />
          <input type="hidden" id="_wpnonce" name="_wpnonce" value={optionNonce} />
          <input type="hidden" name="_wp_http_referer" value={referrer} />
          { isEnabled &&
            <strong><a href={dashboardUrl}>Go to Content Performance</a></strong>
          }
          <table className="form-table">
            <tbody>
              <tr>
                <th scope="row"><label htmlFor="status">Content Performance Status</label></th>
                <td>
                  <p id="status">
                    { isEnabled ?
                      <span className="status-enabled">Enabled</span>
                      :
                      <span className="status-disabled">Paused</span>
                    }
                  </p>
                  { isEnabled &&
                    <p className="description">To disable, contact WP Engine Support 24/7 through the User Portal.</p>
                  }
                </td>
              </tr>
              { !isKeyPairEnabled ?
                [
                  <tr key="1">
                    <th scope="row"><label htmlFor="google_analytics">WP Engine Auth.</label></th>
                    <td>
                      <p>
                          Please enter the Content Performance access and secret keys generated for you in the WP Engine User Portal. You can find them
                        {' '}<a href={keysUrl}>here</a>.
                      </p>
                    </td>
                  </tr>,
                  <tr key="2">
                    <td></td>
                    <td>
                      <label htmlFor="wpe-access-key"><b>Access key</b></label>
                      <br/>
                      <input
                        id="wpe-access-key"
                        className="regular-text code"
                        maxLength="36"
                        autoComplete="off"
                        name={`${configOption}[access_key]`}
                        defaultValue={accessKey}
                      />
                    </td>
                  </tr>,
                  <tr key="3">
                    <td></td>
                    <td>
                      <label htmlFor="wpe-secret-key"><b>Secret key</b></label>
                      <br/>
                      <input
                        id="wpe-secret-key"
                        type="password"
                        maxLength="88"
                        autoComplete="new-password"
                        className="regular-text code"
                        name={`${configOption}[secret_key]`}
                        defaultValue={secretKey}
                      />
                    </td>
                  </tr>
                ]
                :
                [
                  <tr key="1">
                    <th scope="row"><label htmlFor="ga-authenticate">Google Account Auth.</label></th>
                    <td>
                      <p>
                        { !isGaAccessEnabled ?
                          <span>You have not connected your Google Analytics account yet. Take a minute and authenticate to see your data.</span>
                          :
                          <span>You have authenticated your account with Google. You can re-authenticate with a different account below.</span>
                        }
                      </p>
                    </td>
                  </tr>,
                  <tr key="2">
                    <td></td>
                    <td>
                      { !isGaAccessEnabled ?
                        <input
                          className="button-secondary"
                          type="submit"
                          id="ga-authenticate"
                          name={`${configOption}[authenticate]`}
                          value="Connect Google Analytics"
                          disabled={disableInputs}
                        />
                        :
                        <input
                          className="button-secondary"
                          type="submit" id="ga-authenticate"
                          name={`${configOption}[authenticate]`}
                          value="Re-Authenticate"
                          disabled={disableInputs}
                        />
                      }
                      <p className="policy">By clicking here and authenticating to your Google Analytics account through the WP Engine Content Performance Service, you authorize WP Engine to use your Google Analytics data to develop or improve the Service. We may also use your Google Analytics data in an aggregated or otherwise anonymized form.</p>
                    </td>
                  </tr>
                ]
              }
              { isKeyPairEnabled && isGaAccessEnabled &&
                  [
                    <tr key="1">
                      <th scope="row"><label htmlFor="google_analytics">Google Analytics</label></th>
                      <td>
                        <p>
                          You are currently measuring the Google Analytics profile below. If this is incorrect, you may choose a different profile in the drop-down.
                        </p>
                      </td>
                    </tr>,
                    <tr key="2">
                      <td></td>
                      <td>
                        <select
                          id="ga-profile-selection"
                          name={`${configOption}[ga_profile_id]`}
                          disabled={disableInputs}
                          value={viewId}
                          onChange={this.updateViewId}
                        >
                          <option value="">-- none --</option>
                          { profiles.map(profile => (
                            <option key={profile.id} value={profile.id}>
                              {profile.name}
                            </option>
                          ))}
                        </select>
                        <span
                          className={classNames('spinner', { ['is-active']: isLoadingProfiles })}
                        ></span>
                      </td>
                    </tr>,
                    <tr key="3">
                      <th scope="row"><label htmlFor="view_access">View Access</label></th>
                      <td>
                        <fieldset>
                          {Object.keys(roles).map(key => ([
                            <label htmlFor={key}>
                              <input
                                type="checkbox"
                                name={`${configOption}[roles][]`}
                                value={key}
                                defaultChecked={roles[key].capabilities.hasOwnProperty(capability)}
                                {...key === 'administrator' ? { readOnly: true, disabled: true } : {}}
                              />
                              {' '}{roles[key]['name']}
                            </label>,
                            <br />,
                          ])
                          )}
                        </fieldset>
                      </td>
                    </tr>,
                    <tr key="4">
                      <th scope="row"><label htmlFor="default-date-range">Default Date Range</label></th>
                      <td>
                        <select id="default-date-range" name={`${configOption}[default_date_range]`} defaultValue={defaultDateRange}>
                          <option value="1-day">Yesterday</option>
                          <option value="7-days">Last 7 Days</option>
                          <option value="30-days">Last 30 Days</option>
                          <option value="90-days">Last 90 Days</option>
                          <option value="365-days">Last 365 Days</option>
                        </select>
                      </td>
                    </tr>,
                    <tr key="5">
                      <th scope="row"><label htmlFor="default-published-date-range">Default Published Date Range</label></th>
                      <td>
                        <select id="default-published-date-range" name={`${configOption}[default_published_date_range]`} defaultValue={defaultPubliedDateRange} >
                          <option value="no-selection">No Selection</option>
                          <option value="7-days">Last 7 Days</option>
                          <option value="30-days">Last 30 Days</option>
                          <option value="90-days">Last 90 Days</option>
                          <option value="365-days">Last Year</option>
                        </select>
                      </td>
                    </tr>
                  ]}
            </tbody>
          </table>
          <p className="submit">
            {/* Show the try again button if there's a non historical fetch or validating error. */}
            { ((!isGaAccessEnabled || !isGaViewEnabled) && !this.isValidatingOrFetching() && viewId !== '') ?
              <input
                className="button button-primary"
                type="submit"
                name={`${configOption}[retry]`}
                value="Validate"
              />
              :
              <input type="submit" name="submit" id="submit" className="button button-primary" value="Save Changes" />
            }
          </p>
        </form>
      </div>
    );
  }
}

Settings.propTypes = {
  optionGroup: PropTypes.string,
  optionNonce: PropTypes.string,
  referrer: PropTypes.string,
  isEnabled: PropTypes.bool,
  dashboardUrl: PropTypes.string,
  isKeyPairEnabled: PropTypes.bool,
  isGaAccessEnabled: PropTypes.bool,
  isGaViewEnabled: PropTypes.bool,
  configOption: PropTypes.string,
  accessKey: PropTypes.string,
  secretKey: PropTypes.string,
  defaultDateRange: PropTypes.string,
  defaultPubliedDateRange: PropTypes.string,
  capability: PropTypes.string,
  keysUrl: PropTypes.string,
  roles: PropTypes.object,
  error: PropTypes.object,
  viewId: PropTypes.string,
  service: PropTypes.object,
};

export default Settings;
