/* global ajaxurl, jQuery, wpecp */
import 'whatwg-fetch';
import isCustomPostType from '../helpers/is-custom-post-type';

export default class ContentPerformanceService {
  constructor(action = 'wpecp_service_request') {
    this.action = action;
  }

  /**
   * Fetch data about a dimension.
   * @param  {DimensionRequest} request The request for the service.
   * @return {promise}                  The promise that will be resolved.
   */
  fetchData(request) {
    return this.dispatch(request.toURI(), request.toQueryArgs())
      .then(this.translateResponse);
  }

  /**
   * Fetch data about a post.
   * @param  {number} id The ID of the post to get information about.
   * @return {promise}   The promise that will be resolved.
   */
  fetchPostData(id) {
    return this.dispatch(`posts/${id}`);
  }

  /**
   * Fetch traffic source data about a post.
   * @param  {DimensionRequest} request The request for the service.
   * @return {promise}                  The promise that will be resolved.
   */
  fetchTrafficSourceData(request) {
    const dimension = isCustomPostType(request.dimension.value) ? 'custom_posts' : request.dimension.value;
    return this.dispatch(`mediums/${dimension}`,  request.toQueryArgs());
  }

  /**
   * Fetch trend data about a dimension.
   * @param  {object} args The args for the request. TODO: Document args.
   * @return {promise}     The promise that will be resolved.
   */
  fetchTrendsData({ dimension, ...args }) {
    let trendArgs = { ...args };
    if (isCustomPostType(dimension)) {
      trendArgs.custom_post_type = dimension;
      dimension = 'custom_posts';
    }

    return this.dispatch(`trends/${dimension}`,  { ...trendArgs, limit: 10, offset: 0 });
  }

  /**
   * Fetch posts for a dimension.
   * @param  {DimensionRequest} request The request for the service.
   * @param  {number}           id      The ID of the taxonomy to get information about.
   * @return {promise}                  The promise that will be resolved.
   */
  fetchDimensionData(request, id) {
    return this.dispatch(`${request.toURI()}/${id}`, request.toQueryArgs())
      .then(this.translateResponse);
  }

  /**
   * Fetch Google Analytics profiles
   * @return {promise}
   */
  fetchProfiles() {
    return this.dispatch('profiles');
  }

  /**
   * Makes a request to the WordPress backend.
   * @param  {string} endpoint The endpoint to hit.
   * @param  {object} args     An object containing the query args.
   * @return {promise}         A promise that will contain the response.
   */
  dispatch(endpoint, args) {
    const query = {
      action: this.action, // Tell WordPress which action to hit.
      nonce: wpecp.nonce, // Nonce needed for verification.
      params: args,
      endpoint,
    };

    // We use ajaxurl which is defined globally by WordPress.
    let promise = new Promise((resolve, reject) => {
      fetch(this.buildUrl(ajaxurl, query), { credentials: 'same-origin' })
        .then(this.checkStatus)
        .then(this.parseJSON)
        .then(this.extractData)
        .then(resolve)
        .catch(reject);
    });

    return promise;
  }

  /**
   * Encodes and appends query string parameters to a url.
   * @param  {string} url  The url.
   * @param  {object} args An object containing the query strings.
   * @return {string}      The url and query strings combined.
   */
  buildUrl(url, args) {
    // Encode and add args if it exists.
    if (typeof args !== 'undefined') {
      url += '?' + jQuery.param(args);
    }
    return url;
  }

  /**
   * Make sure the status code is in the 200 range.
   * @param  {object} response fetch response.
   * @return {object}          Returns the response for chaining.
   */
  checkStatus(response) {
    if (response.ok) {
      return response;
    } else {
      // If statusText is set, use it in the message.
      const error = response.statusText
        ? `${response.statusText}. ${response.status} status code received.`
        : `${response.status} status code received.`;
      return Promise.reject(error, response.json());
    }
  }

  /**
   * Returns the json from a fetch response.
   * @param  {object} response fetch response
   * @return {object}          json object.
   */
  parseJSON(response) {
    return response.json();
  }

  /**
   * Extract the data from the json response. The response also includes the
   * status of the request.
   * @param  {object} json The full json response.
   * @return {array}       The array response.
   */
  extractData(json) {
    if (typeof json.data !== 'undefined' && true === json.success) {
      return json.data;
    } else if ( typeof json.data !== 'undefined' && typeof json.data !== 'object' ) {
      return Promise.reject(json.data, json);
    } else if ( json === 0 ) {
      // This happens when WordPress can't find our ajax endpoint.
      return Promise.reject('ajax endpoint not found.', json);
    } else {
      return Promise.reject('data not found in response.', json);
    }
  }

  /**
   * Take the response data and translate the key/value names.
   * @param  {array} data  Items from the response.
   * @return {array}       The translated data.
   */
  translateResponse(response_data) {
    response_data.data = response_data.data.map(item => {
      let percentage = response_data.total_value ? ((item.total / response_data.total_value) * 100) : null;
      return {
        label: item.label,
        value: item.total, percentage: percentage,
        id: item.id,
        post_date: item.post_date,
        post_modified: item.post_modified,
      };
    });
    return response_data;
  }
}
