import React from 'react';
import ReactDOM from 'react-dom';
import Settings from './components/settings';
import CPService from './services/content-performance';

document.addEventListener('DOMContentLoaded', () => {
  ReactDOM.render(
    <Settings
      optionGroup={window.wpecp.optionGroup}
      optionNonce={window.wpecp.optionNonce}
      referrer={window.wpecp.referrer}
      isEnabled={'1' === window.wpecp.isEnabled}
      isKeyPairEnabled={'1' === window.wpecp.isKeyPairEnabled}
      isGaAccessEnabled={'1' === window.wpecp.isGaAccessEnabled}
      isGaViewEnabled={'1' === window.wpecp.isGaViewEnabled}
      dashboardUrl={window.wpecp.dashboardUrl}
      configOption={window.wpecp.configOption}
      accessKey={window.wpecp.accessKey}
      secretKey={window.wpecp.secretKey}
      defaultDateRange={window.wpecp.defaultDateRange}
      defaultPubliedDateRange={window.wpecp.defaultPubliedDateRange}
      roles={window.wpecp.roles}
      capability={window.wpecp.capability}
      keysUrl={window.wpecp.keysUrl}
      error={window.wpecp.error}
      viewId={window.wpecp.viewId}
      service={new CPService('wpecp_settings')}
    />,
    document.getElementById('wpecp-react-settings-component')
  );
});
